import { useEffect } from 'react';
import Head from 'next/head';
import { useRouter } from 'next/router';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import '../assets/styles.css';

// Import Google Fonts
const fontHref = 'https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap';

// Default SEO configuration
const defaultSEO = {
  title: 'Adoods - Premium T-Shirts & Apparel',
  description: 'Discover our premium collection of t-shirts. Comfortable, stylish, and made to last.',
  keywords: 't-shirts, apparel, fashion, clothing, premium t-shirts',
  siteUrl: 'https://youraddress.com',
  image: 'https://youraddress.com/img/banner.png',
  twitter: '@youraddress'
};

// Add this CSS to prevent any global click handlers from interfering with navigation
const globalStyles = `
  html, body, #__next {
    height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }
  
  a, button {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
`;

export default function App({ Component, pageProps }) {
  const router = useRouter();
  
  // Set up page view tracking
  useEffect(() => {
    const handleRouteChange = (url) => {
      // Here you can add analytics tracking
      // Example: window.gtag('config', 'GA_MEASUREMENT_ID', { page_path: url });
    };
    
    router.events.on('routeChangeComplete', handleRouteChange);
    return () => {
      router.events.off('routeChangeComplete', handleRouteChange);
    };
  }, [router.events]);

  // Get page-specific SEO data or use defaults
  const seo = {
    ...defaultSEO,
    ...(pageProps.seo || {})
  };

  return (
    <>
      <Head>
        <title>{seo.title}</title>
        <meta name="description" content={seo.description} />
        <meta name="keywords" content={seo.keywords} />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={defaultSEO.title} />
        <meta name="twitter:description" content={defaultSEO.description} />
        <meta name="twitter:image" content={defaultSEO.image} />
        <meta name="twitter:site" content={defaultSEO.twitter} />
        <link rel="preconnect" href="https://fonts.googleapis.com" />
        <link rel="preconnect" href="https://fonts.gstatic.com" crossOrigin="true" />
        <link href={fontHref} rel="stylesheet" />
        <style>{globalStyles}</style>
      </Head>
      
      <Navbar />
      <main>
        <Component {...pageProps} />
      </main>
      <Footer />
    </>
  );
}
